<!-- 条件选品种弹窗 -->
<template>
    <div>
        <Modal :value="isShow" :title="'条件选品种'" width="600" :mask-closable="true" @on-confirm="confirm" @on-cancel="close()">
        <div class="" style="padding:0 10px;">
            <div class="cloa pb5 mb5" style="border-bottom:1px solid #333;">技术面</div>
            <Form :model="form" :label-width="100" class="cloa">
                <FormItem label="价格">
                    <div class="flexBox">
                        <Input v-model="form.account" placeholder="0.00" class="w100 mr10"></Input>~
                        <Input v-model="form.account" placeholder="0.00" class="w100 mr10 ml10"></Input>元
                    </div>
                </FormItem>
                <FormItem label="涨跌幅">
                    <div class="flexBox">
                        <Input v-model="form.account" placeholder="0" class="w100 mr10"></Input>~
                        <Input v-model="form.account" placeholder="0" class="w100 mr10 ml10"></Input>%
                    </div>
                </FormItem>
                <FormItem label="成交量">
                    <div class="flexBox">
                        <Input v-model="form.account" placeholder="0.00" class="w100 mr10"></Input>~
                        <Input v-model="form.account" placeholder="0.00" class="w100 mr10 ml10"></Input>元
                    </div>
                </FormItem>
                <FormItem label="成交额">
                    <div class="flexBox">
                        <Input v-model="form.account" placeholder="0.00" class="w100 mr10"></Input>~
                        <Input v-model="form.account" placeholder="0.00" class="w100 mr10 ml10"></Input>元
                    </div>
                </FormItem>
                <FormItem label="振幅">
                    <div class="flexBox">
                        <Input v-model="form.account" placeholder="0" class="w100 mr10"></Input>~
                        <Input v-model="form.account" placeholder="0" class="w100 mr10 ml10"></Input>%
                    </div>
                </FormItem>
            </Form>
            <!-- <div class="cloa pb5 mb5 " style="border-bottom:1px solid #333;">基本面</div>
            <Form :model="form" :label-width="100" class="cloa">
                <FormItem label="提醒频率">
                    <Select v-model="form.mode_5" class="" style="width:auto;" placeholder="请选择">
                        <Option value="mode_51">每日一次</Option>
                        <Option value="mode_52">仅提醒一次</Option>
                    </Select>
                </FormItem>
            </Form> -->
        </div>
    </Modal>
    </div>
</template>

<script>
export default {
    name: 'WebWarnPop',
    model:{
        prop:'isShow',
        event:'change'
    },
    props: {
        //连接钱包弹窗，是否显示
        isShow:{
            type:Boolean,
            default:false
        },
    },
    data() {
        return {
            // isShow:true,
            form:{
                mode_2:'mode_21'
            },
        };
    },
    mounted() {
        
    },
    methods: {
        confirm(){
            this.$emit('confirm',this.form)
        },
        //关闭
        close(){
            this.$emit("change",false);
        },
    },
};
</script>

<style lang="less" scoped>
:deep(.ivu-modal-header-inner){
    color:#ccc;
}
:deep(.ivu-form-item){
    margin-bottom: 10px;
}
</style>